import util from '../../utils/util.js'
export default {
	data() {
		return {
			pay_log: '',
			time: ''
		};
	},
	onLoad(options) {
		this.$data.pay_log = JSON.parse(options.pay_log);
		this.$data.pay_log.change_amount = util.formatMoney(this.$data.pay_log.change_amount);
		this.$data.time = util.getNowFormatDate();
	},
	onShow() {

	},
	methods: {
		/**
		 * 返回首页
		 */
		goIndex() {
			uni.reLaunch({
				url: '../index/index?from=0'
			});
		},
		// 去订单详情
		goFindOrderDetail() {
			uni.reLaunch({
				url: '../index/index?from=2'
			});
		},
	},
}
